
.footer {
  margin-top: 53px;
  background-color: $footer-col;
  min-height: 295px;
}

.footer__head {
  font-family: "Playfair display", 'Times New Roman', serif;
  font-size: 28px;
  line-height: 35px;
  font-weight: 700;
  letter-spacing: 1px;
  color: $body-col;
  text-align: center;
  padding-top: 26px;
}

.footer-nav.site-list {
  margin-left: 135px;
  margin-right: 135px;
  padding-bottom: 43px;
  margin-top: 34px;
  margin-bottom: 42px;
  border-bottom: 1px solid $body-col;
}

.footer__social {
  display: flex;
  max-width: 170px;
  margin: 0 auto;
}

.footer__elem {
  margin-right: 14px;
}
