.intro {
  background-image: url(../img/Hotel-water.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 464px;
  color: $body-col;
}

.intro__title {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 72px;
  line-height: 72px;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-align: center;
  padding-top: 138px;
  position: relative;
  &::before {
    content: "";
    position: absolute;
    left: 693px;
    top: 46px;
    background-image: url(../img/Hotel-icon.svg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 55px;
    height: 55px;
  }

}

.intro__text {
  margin: 0 auto;
  margin-top: 140px;
  text-align: center;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  width: 456*100%/1440;

}
