
.theater {
  min-height: 556px;
  background-color: $theater-back;
  background-image: url("../img/Ellipse-icon.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.theater__wrap {
  max-width: 370px;
  margin: 0 auto;

}

.theater__title {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 17px;
  line-height: 21px;
  font-weight: 700;
  letter-spacing: 2px;
  text-align: center;
  padding-top: 36px;
  margin-bottom: 35px;
}

.theater__bonus-item {
  margin-bottom: 50px;
  position: relative;
  &::after{
    content: "";
    position: absolute;
    width: 100px;
    height: 2px;
    background-color: $theater-back;

    top: 102px;
    left: 134px;
  }
  &--last{
    margin-bottom: 0;
    &::after{
      width: 0;
      height: 0;
    }
  }
}

.theater__head {
  font-family: "Playfair display", 'Times New Roman', serif;
  font-size: 23px;
  line-height: 27px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 10px;
}

.theater__text {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  text-align: center;
}

.theater__btn {
  position: relative;
  width: 170px;
  height: 47px;
  margin: 0 auto;
  margin-top: 47px;
  padding-top: 13px;
  padding-left: 30px;
  background-color: $text-orange;
  &::after{
    content: "";
    position: absolute;
    width: 24px;
    height: 25px;
    right: 25px;
    background-image: url("../img/Arrow-icon.svg");
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.theater__link {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 700;
  text-transform: uppercase;
  color: $body-col;
}
