@font-face {
  font-family: "Playfair display";
  src:
    url("../fonts/playfair-display.woff") format("woff"),
    url("../fonts/playfair-display.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Playfair display";
  src:
    url("../fonts/playfair-display-bold.woff") format("woff"),
    url("../fonts/playfair-display-bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src:
    url("../fonts/roboto.woff") format("woff"),
    url("../fonts/roboto.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src:
    url("../fonts/roboto-bold.woff") format("woff"),
    url("../fonts/roboto-bold.woff") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
