.review {
  margin-left: 137px;
  margin-right: 105px;
  margin-bottom: 51px;
}
.review__title {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 17px;
  line-height: 21px;
  font-weight: 700;
  letter-spacing: 2px;
  text-align: center;
  margin-top: 44px;
  margin-bottom: 19px;
  margin-right: 33px;
}
.review__wrap {
  display: flex;
  justify-content: space-between;
}
.review__elem {
  width: 33.33%;
}
.review__pic-wrap {
  margin-bottom: 20px;
}

.review__text-wrap {
  max-width: 370px;
}
.review__head {
  font-family: "Playfair display", 'Times New Roman', serif;
  font-size: 23px;
  line-height: 27px;
  font-weight: 700;
  margin-bottom: 12px;
  &--orange {
    color: $text-orange;
  }
}
.review__tetx {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  text-align: left;
}
