.site-list {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  margin-top: 50px;
  margin-bottom: 46px;
}

.site-list__item {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 15px;
  line-height: 21px;
  font-weight: 400;
  color: $foot-cont;
  &:hover {
    border-bottom: 1px solid $foot-cont;
  }
  &--footer {
    color: $body-col;
    &:hover {
      border-bottom: 1px solid $body-col;
    }
  }
}
